<template>
    <div>
        <MHeader :show="true" class="MHeader">列表页</MHeader>
        <div class="container" ref='scroll' @scroll='loadMore'>
            <ul>
                <router-link v-for="(book,index) in books" :to="{name:'detail',params:{uid:book.bookId}}" tag="li" :key="index">
                    <img v-lazy="book.bookCover">
                    <div class="info">
                        <h4>{{book.bookName}}</h4>
                        <p class='bookInfo'>{{book.bookInfo}}</p>
                        <p class='bookPrice'>{{book.bookPrice | toFixed}}</p>
                        <button @click.stop="remove(book.bookId)" class='btn'>删除</button>
                    </div>
                </router-link>
                <li class="addmore" @click="more">{{msg}}</li>
            </ul>
        </div>
    </div>
</template>


<script>
    import MHeader from '../base/MHeader.vue';
    import {pagination,removeBook} from '../api';

    var animate=(function(){
        // 这里的运动轨迹只提供了一种，可以扩展多种运动轨迹
        /*
            t：使用的时间time
            b：起始位置begin
            c：总长度change=目标位置-起始位置
            d：总时间duration
        */
        function linear(t,b,c,d){
            return t/d*c+b;
        }

        function animate(options){
            // 初始化参数(在ES6中可以使用解构赋值，考虑到兼容性，这里不使用解构赋值)
            var _default={
                ele:null,
                target:{},
                duration:1000,
                callBack:null,
                effect:linear
            }

            for(var key in options){
                if(options.hasOwnProperty(key)){
                    _default[key]=options[key];
                }
            }

            var ele=_default.ele,
                target=_default.target,
                duration=_default.duration,
                callBack=_default.callBack,
                effect=_default.effect;


            var t=0,
                d=duration||1000,
                b={},
                c={};

            for(var key in target){
                if(target.hasOwnProperty(key)){
                    b[key]=css(ele,key);
                    c[key]=target[key]-b[key];
                }
            }
            //在设置新动画之前，先把之前正在运行的动画清除掉，防止当前元素相同的动画共存
            clearInterval(ele.animateTimer);
            ele.animateTimer=setInterval(function(){
                var cur={};
                t+=17;
                if(t>=d){
                    css(ele,target);
                    clearInterval(ele.animateTimer);
                    callBack && callBack();
                    return;
                }

                for(var key in target){
                    if(target.hasOwnProperty(key)){
                        cur[key]=effect(t,b[key],c[key],d);
                    }
                }
                css(ele,cur);
            },17);
        }

        return animate;
    })();

    export default {
        data() {
            return {
                books:[],
                offset:0, // 偏移量
                hasMore:true, // 是否有更多
                isLoading:false,
                msg:'点击加载更多'
            }
        },
        mounted(){
            let scroll=this.$refs.scroll;
            let top=scroll.offsetTop;
            scroll.addEventListener('touchstart',(e)=>{
                // 滚动条在最顶端时，或者 当前盒子在顶端的时候，才继续走
                if(scroll.scrollTop!==0 || scroll.offsetTop!==top) return;
                let start=e.touches[0].pageY;
                let distance=0;
                let move=(e)=>{
                    let current = e.touches[0].pageY;
                    distance=current-start;// 求拉动的距离，负的就不要，只要从上往下拉的。
                    if(distance>0){
                        // 限制下拉距离
                        if(distance<=150){
                            scroll.style.top=distance+top+"px";
                        }else{
                            distance=150;
                            scroll.style.top=150+top+"px";
                        }
                    }else{
                        // 如果不在考虑范围内，移除掉move和end事件
                        scroll.removeEventListener('touchmove',move);
                        scroll.removeEventListener('touchend',end);
                    }
                };
                let end=(e)=>{
                    clearInterval(this.scrollTimer);
                    this.scrollTimer=setInterval(()=>{
                        if(distance<=0){
                            clearInterval(this.scrollTimer);
                            distance=0;
                            scroll.style.top=top+"px";
                            scroll.removeEventListener('touchmove',move);
                            scroll.removeEventListener('touchend',end);
                            this.books=[];
                            this.offset=0;
                            this.hasMore=true;
                            this.getData();
                            this.msg="正在刷新";
                            return;
                        }
                        distance=distance/1.5;
                        distance<1?distance=0:null;
                        scroll.style.top=top+distance+"px";
                    },17);
                };
                scroll.addEventListener('touchmove',move);
                scroll.addEventListener('touchend',end);
            },false);
        },
        created(){
            this.getData();
        },
        methods: {
            loadMore(){
                clearTimeout(this.timer);
                this.timer=setTimeout(()=>{
                    let {scrollTop,scrollHeight,clientHeight}=this.$refs.scroll;
                    if(scrollTop+clientHeight+20>scrollHeight){
                        this.getData();
                    }
                },50);
            },
            more(){
                if(!this.hasMore){
                    this.msg="已加载到底部";
                    return;
                }
                this.getData();
            },
           async getData(){
               if(this.hasMore && !this.isLoading){
                   this.msg="加载中..."
                   this.isLoading=true;
                   let {data}=await pagination(this.offset);
                   this.books=[...this.books,...data.books];
                   this.hasMore=data.hasMore;
                   this.offset=this.books.length;// 维护偏移量，就是总数的长度
                   this.isLoading=false;
                   this.msg="点击加载更多"
               }
            },
            async remove(id){
               await removeBook(id); // 删除某一项
                // 要删除前台数据
                this.books=this.books.filter(item=>item.bookId!==id);
            }
        },
        computed: {},
        components: {
            MHeader
        },
        filters:{
            toFixed(price){
                if(isNaN(price))return;
                return '￥'+ price.toFixed(2);
            }
        }
    }
</script>


<style  lang="less">
    .container{
        width:100%;
        position:fixed;
        top:40px;
        bottom:50px;
        overflow:auto;
        background:#F8F9F9;
        padding:10px;

        li{
            margin-bottom:10px;
            border-bottom:1px solid #ddd;
            padding:10px 0 15px 0;
            &:after{
                content: "";
                display:block;
                clear:both;
            }
            img{
                float:left;
                width:80px;
                height:119px;
                box-shadow: 0 0 5px;
            }
            .info{
                width:70%;
                margin-left:10px;
                /*background:gray;*/
                float:left;
                h4{color:#333;font-size:16px; padding-right:20px;}
                .bookInfo{
                    color:#a6a6a6;
                    font-size:14px;
                    margin-top:5px;
                    padding-right:13px;
                }
                .bookPrice{
                    color:#68ABB7;
                    font-size:14px;
                }
                .btn{
                    background:#999;
                    color:white;
                    border: 0px solid transparent;
                    padding:5px 10px;
                    border-radius:5px;
                    margin:10px;

                }
            }
        }
    }
    .MHeader{
        position:absolute;
        top:0;
        left:0;
        z-index:101;
    }
    .addmore{
        text-align: center;
        color:#8e8e8e;
        background:#fff;
        margin-left:-10px;
        margin-top:-10px;
    }
</style>



